.home{
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100% - 60px);
    padding-left: 15px;
    padding-right: 15px;
    margin-right: 15px;
    margin-left: 15px;
    h1{
        color: #0000ee;
        text-align: center;
    }
    .page{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .link{
            text-decoration: none;
            color: rgb(166, 165, 165);
            border: 2px solid black;
            margin: 10px;
            padding: 10px;
            width: 450px;
            font-weight: bold;
            font-size: x-large;
            border-radius: 10px;
            text-align: center;
            cursor: pointer;
            &:hover{
                color: red;
            }
        }
    }
    .img{
        margin: 35px;
        border-radius: 50%;
        @media (max-width: 767px) {
            margin: 0;
        }
    }
}