.mawane3-koboul{
    display: none;
    width: 600px;
    margin: 30px auto;
    padding: 20px;
    border: 2px solid #aaa;
    h1{
        text-align: center;
        color: #0000ee;
    }
    p{
        font-size: xx-large;
        font-family: cursive;
        margin-bottom: 0;
        font-style: oblique;
        color: #920b0bb5;
    }
    ol{
        margin-top: 0;
        margin-right: 30px;
        li{
            font-family: cursive;
            font-size: 25px;
            color: red;
            span{
                color: black;
            }
        }
    }
    @media (max-width: 767px) {
        width: 365px;
    }
}

